// eslint-disable-next-line
function AppInstallBanner() {
  let host = ""
  if (isBrowser()) {
    host = location.hostname
  }
  if (host !== "evans-energy-b9ba239f1ce7.herokuapp.com") {
    return null
  }

  return (
    <div className="w-full bg-red-600 text-white justify-center flex font-bold p-4">
      <a href="/assets/evans_energy2.apk">Install the app</a>
    </div>
  )
}

// eslint-disable-next-line
function fallbackRender({ error, resetErrorBoundary }) {
  // Call resetErrorBoundary() to reset the error boundary and retry the render.



  if (H) {
    H.track("Error saving well data", {
      error: error.message,
      trace: error.stack,
    });
  }

  fetch('/ev', {
    method: 'POST',
    headers,
    body: JSON.stringify({
      track: {
        client_time: new Date().toISOString(),
        event_type: 'client_error',
        // fp: result.visitorId,
        properties: {
          path: location.pathname,
          error: error.message,
          trace: error.stack,
        }
      }
    })
  })
  // resetErrorBoundary()


  return (
    <div role="alert">
      <p>Something went wrong:</p>
      <pre style={{ color: "red" }}>{error.message}</pre>
    </div>
  );
}


